.main {
    background-color: #334;
    padding: 20px;
    border-radius: 20px;

    h1 {
        text-align: center;
        margin: 0px;
        margin-bottom: 20px;
    }

    div {
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;

        h2 {
            margin: 0px;
            margin-bottom: 10px;
        }

        span {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            flex-direction: row;
        }
    }

    .actions {
        form {
            &:not(:last-of-type) {
                margin-right: 10px;
            }
    
            input {
                padding: 12px 17px;
                border-radius: 7px;
                border: 1px solid #556;
                cursor: pointer;
                background: #334;
                transition: background-color 70ms ease-out;
                
                &.delete {
                    border-color: #e44;
                }
            }
        }
    }
}

body:not(.touch) {
    .delete:hover {
        background-color: #e44;
        color: #223;
    }
}

body.touch {
    .delete:active {
        background-color: #e44;
        color: #223;
    }
}